import styles from "./security.module.css"
import { getAuth, signOut } from "firebase/auth";
import { doc, setDoc, getFirestore, collection, getDocs} from "@firebase/firestore";
import App from "../../../service/Firebase";
import { updateDoc } from "firebase/firestore";


export default function Security (props) {

    const user = props.usuario
    const usuarios = props.users
    const auth = getAuth();
    const db = getFirestore(App)

    const Logout = () => {
        signOut(auth)
        .then(()=> {
            window.location.href = '/'
        })
        .catch(()=> {

        })
    }

    const AceitaOuRecusa = async(obj) => {
        await updateDoc(doc(db, "convites", obj.id), {
            state: obj.state ? false : true
        });
        window.location.reload()
    }
    




    return (
        <>
            {user && user.security && 
            <div>
                <h5>Segurana - {user.nome}</h5>
                <button
                className={styles.btn_logout}
                onClick={Logout}
                >Sair</button>
                <div
                className={styles.cont_List}
                >
                    <h5>Lista de convidados</h5>
                    <ol
                    className={styles.list}
                    >
                        {usuarios && usuarios.map(dados => {
                            return (
                                <li
                                key={dados.id}
                                className={`${dados.state && styles.ok} ${styles.li}`}
                                >
                                    <p>{dados.nome}</p>
                                    <p>{dados.state ? 'confirmado' : 'pendente'}

                                    {user.admin && 
                                    <span>
                                        {dados.state ?
                                        <button
                                        className={styles.button}
                                        onClick={() => AceitaOuRecusa(dados)}
                                        >
                                            cancelar
                                        </button>
                                        :
                                        <button
                                        className={styles.button}
                                        onClick={() => AceitaOuRecusa(dados)}
                                        >
                                            confimar
                                        </button>
                                        }
                                    </span>
                                    }

                                    </p>
                                    
                                </li>
                            )
                        })}
                    </ol>
                </div>
            </div>
            }
        </>
    )
}