import { useEffect, useState } from "react";
import App from "../../../service/Firebase";
import { collection, getDocs, getFirestore } from "firebase/firestore";
import { useParams } from "react-router-dom";


export default function Verificacao () {


    const [user, setUser] = useState()
    const [users, setUsers] = useState()
    const db = getFirestore(App)
    const UserCollection = collection(db, "convites")
    let { userid } = useParams();


    useEffect(()=> {
        App.auth().onAuthStateChanged(user => {
            if (user) {
                setUser(user)
            }
        })
        const getUsers = async () => {
            const data = await getDocs(UserCollection);
            setUsers((data.docs.map((doc) => ({...doc.data(), id: doc.id}))))
        };
        getUsers()
        
    },[])


    const UsuarioLogado = user && users && users.filter(dados => {
        return dados.email.toLowerCase() == user.email
    })  

    const UsuarioConvite = user && users && users.filter(dados => {
        return dados.id == userid
    })  


    return (
        <>
            {user ? 
            
            
            
            UsuarioLogado && UsuarioLogado[0].security ?
            <div>
                <p>segurana</p>
                {UsuarioConvite && 
                UsuarioConvite[0].state ?

                <div>
                    <p>convidado</p>
                    <p>{UsuarioConvite[0].nome}</p>
                </div>:

                <div>
                    <p>desconvidado</p>
                </div>

                }



            </div>
            :
            <div>
                <p>sem aceesso</p>
            </div>
            
            
            
            
            
            
            
            :
            <button>Fazer login</button>
            }
        </>
    )
}