import styles from "./FormCadastro.module.css"
import stylesB from "../pages/Home.module.css"
import Cadastro from "../modals/Cadastro"
import { getAuth, signInWithEmailAndPassword } from "firebase/auth";
import { useEffect, useState } from "react";
import App from "../../service/Firebase";

export default function FormCadastro () {


    const [nome, setNome] = useState()
    const [email, setEmail] = useState()
    const [password, setPassword] = useState()


    useEffect(()=> {
        App.auth().onAuthStateChanged(user => {
            if (user) {
                window.location.href = 'convite'
            }
        })
        
    },[])
    
    const [user, setUser] = useState(null);
    function LoginW(event) {
        event.preventDefault();
        App.auth().signInWithEmailAndPassword(email, password)
        .then((usuario)=> {setUser(usuario)})
        .catch(()=> {
            alert('Usurio ou senha incorretos!')
        })
    }


    return (
        <>
            <div
            className={styles.tot}
            >
                
                <div
                className={styles.container}
                >
                
                    <h3
                    className={styles.title}
                    >Seja Bem vindo</h3>
                    <div className={styles.line}/>
                
                        <div
                        className={styles.containerForm}
                        >


                            <form >
                                <div className={`${styles.form__group} ${styles.field}`}>
                                    <input type="email" className={styles.form__field} id='email1'
                                    onChange={(el)=> setEmail(el.target.value)}
                                    required />
                                    <label for="email" className={styles.form__label}>Email *</label>
                                </div>
                                <div className={`${styles.form__group} ${styles.field}`}>
                                    <input type="password" className={styles.form__field}
                                    onChange={(el)=> setPassword(el.target.value)}
                                    id='Senha' required />
                                    <label for="Senha" className={styles.form__label}>Senha *</label>
                                </div>
                                <button
                                onClick={(ev) => LoginW(ev)}
                                className={`${styles.button} ${stylesB.button}`}
                                >Confirmar</button>
                            </form>


                            <button type="button" className={`btn btn-primary ${styles.buttonCreate}`} data-bs-toggle="modal" data-bs-target="#exampleModal">
                            Criar conta
                            </button>

                            <div class="modal fade" id="exampleModal" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
                                <div class="modal-dialog">
                                    <div class="modal-body">
                                        <Cadastro/>
                                    </div>
                                </div>
                            </div>
                        </div>
                </div>
            </div>
        </>
    )
}