import { getAuth, createUserWithEmailAndPassword, signInWithEmailAndPassword } from "firebase/auth";
import { useEffect, useState } from "react";
import styles from "./Cadastro.module.css"
import App from "../../service/Firebase"
import { doc, setDoc, getFirestore, collection, getDocs} from "@firebase/firestore";
import stylesInput from "../formularios/FormCadastro.module.css"


export default function Login () {

    const [nome ,setNome] = useState()
    const [email, setEmail] = useState()
    const [password, setPassword] = useState()
    const [users, setUsers] = useState()
    const db = getFirestore(App)
    const UserCollection = collection(db, "convites")

    useEffect(()=>{
        const getUsers = async () => {
            const data = await getDocs(UserCollection);
            setUsers((data.docs.map((doc) => ({...doc.data(), id: doc.id}))))
        };
        getUsers()
    },[])

    const auth = getAuth();
    
    const criarConta = async() => createUserWithEmailAndPassword(auth, email, password) 
      .then((userCredential) => {
        const user = userCredential.user;
        return user
      })
      .catch((error) => {
        const errorCode = error.code;
        const errorMessage = error.message;
        // ..
      });


      const id = users && users.map(dados => {return Number(dados.id)})

      var max = users && id.reduce(function (a, b) {
        return Math.max(a, b);
      }, -Infinity);


      const SignIn = async() => {
        await setDoc(doc(db, `convites`, `${max+1}`), {
            id: max+1,
            email: email,
            nome,
            state: false,
            admin:false,
            security:false
        });

        await criarConta()

      }




    return (
        <>
        <div className="modal-dialog">
            <div className="modal-content">
            <div className="modal-header">
                <h1 className={`${styles.title} modal-title fs-5`} id="exampleModalLabel">Criar minha conta</h1>
                <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
            </div>
            <div className="modal-body">
                <form
                className={styles.form}
                >

                  <div className={`${stylesInput.form__group} ${stylesInput.field}`}>
                      <input type="name" className={stylesInput.form__field} id='name'
                      onChange={(el)=> setNome(el.target.value)}
                      required />
                      <label for="nome" className={stylesInput.form__label}>Nome Completo *</label>
                  </div>
                  <div className={`${stylesInput.form__group} ${stylesInput.field}`}>
                      <input type="email" className={stylesInput.form__field} id='email'
                      onChange={(el)=> setEmail(el.target.value)}
                      required />
                      <label for="email" className={stylesInput.form__label}>Email *</label>
                  </div>

                  <div className={`${stylesInput.form__group} ${stylesInput.field}`}>
                      <input type="password" className={stylesInput.form__field} id='password'
                      onChange={(el)=> setPassword(el.target.value)}
                      required />
                      <label for="password" className={stylesInput.form__label}>Senha *</label>
                  </div>
                </form>
            </div>
            <div class="modal-footer">
                <button type="button" className={`${styles.buttonCancelar} btn btn-secondary`} data-bs-dismiss="modal">Cancelar</button>
                <button type="button" className={`${styles.buttonEnviar} btn btn-primary`}
                onClick={SignIn}>Enviar</button>
            </div>
            </div>
        </div>
        </>
    )
}