import { Link } from "react-router-dom";
import {  getAuth, signOut } from "firebase/auth";

export default function FormAnalise () {

    const auth = getAuth();



    const Logout = () => {
        signOut(auth)
        .then(()=> {
            window.location.href = '/'
        })
        .catch(()=> {

        })
    }


    return (
        <>
            <div>
                <h4>Estamos analisando seu Convite</h4>
                <Link
                to="/"
                >Incio</Link>
                <button
                onClick={Logout}
                >sair</button>
            </div>
        </>
    )
}