import { useEffect, useState } from "react";
import styles from "./Admin.module.css"
import stylesInput from "../../formularios/FormCadastro"
import App from "../../../service/Firebase";
import { doc, setDoc, getFirestore, collection, getDocs} from "@firebase/firestore";
import { getAuth, signOut } from "firebase/auth";
import { updateDoc } from "firebase/firestore";
import Loading from "../../../components/Loading"
import Security from "./Security"


export default function Login () {

    
    const [user, setUser] = useState()
    const [users, setUsers] = useState()
    const auth = getAuth();
    const db = getFirestore(App)
    const UserCollection = collection(db, "convites")

    useEffect(()=> {
        App.auth().onAuthStateChanged(user => {
            if (user) {
                setUser(user)
            }
        })
        const getUsers = async () => {
            const data = await getDocs(UserCollection);
            setUsers((data.docs.map((doc) => ({...doc.data(), id: doc.id}))))
        };
        getUsers()
        
    },[])

    const Logout = () => {
        signOut(auth)
        .then(()=> {
            window.location.href = '/'
        })
        .catch(()=> {

        })
    }
    const UsuarioLogado = user && users && users.filter(dados => {
        return dados.email.toLowerCase() == user.email
    })  

    const AceitaOuRecusa = async(obj) => {
        await updateDoc(doc(db, "convites", obj.id), {
            state: obj.state ? false : true
        });
        window.location.reload()
    }

    return (
        <>
        {!UsuarioLogado ? <Loading/> : 
        <Security usuario={UsuarioLogado && UsuarioLogado[0]} users={users && users}/>
        }
        </>
    )
}