
import { BrowserRouter, Routes, Route } from "react-router-dom";
import Home from "./components/pages/Home";
import Convidado from "./components/pages/Convidado";
import FormCadastro from "./components/formularios/FormCadastro";
import Admin from "./components/pages/ADMIN/Admin";
import Verificacao from "./components/pages/ADMIN/Verificacao";

function App() {
  return (
      <BrowserRouter>
          <Routes>
            <Route path="/" element={<Home/>}/>
            <Route path="/login" element={<FormCadastro/>}/>
            <Route path="/convite" element={<Convidado/>}/>
            <Route path="/admin" element={<Admin/>}/>
            <Route path="/verificacao/:userid" element={<Verificacao/>}/>
          </Routes>
      </BrowserRouter>
  );
}

export default App;
