import styles from "./FormConfirm.module.css"
import { useEffect, useState } from "react";
import App from "../../service/Firebase";
import { getAuth, signOut } from "firebase/auth";
import { doc, setDoc, getFirestore, collection, getDocs} from "@firebase/firestore";
import FormAnalise from "./FormAnalise"
import Loading from "../Loading"
import QRCode from "react-qr-code";


export default function FormConfirm () {


    const db = getFirestore(App)
    const UserCollection = collection(db, "convites")
    const [users, setUsers] = useState()
    
    const auth = getAuth();
    const [user, setUser] = useState()
    useEffect(()=> {
        App.auth().onAuthStateChanged(user => {
            if (user) {
                setUser(user)
            }
        })
        const getUsers = async () => {
            const data = await getDocs(UserCollection);
            setUsers((data.docs.map((doc) => ({...doc.data(), id: doc.id}))))
        };
        getUsers()
        
    },[])
    const Logout = () => {
        signOut(auth)
        .then(()=> {
            window.location.href = '/'
        })
        .catch(()=> {

        })
    }

    const UsuarioLogado = users && users.filter(dados => {
        return ((dados.email).toLowerCase() == user.email)
    })
    
    
    return (
        <>

            {!UsuarioLogado ? <Loading/>:
            
            UsuarioLogado[0].state ?
                <div>
                    <img src='https://www.papeisetudomais.com.br/wp-content/uploads/2023/09/terracota.webp'
                    className={styles.tot}
                    />
                        
                    <div>
                        <div className={styles.container}>
                        <h2
                        className={styles.title}
                        >Presena confirmada!</h2>
                        <div className={styles.line}/>
                        <div>
                            <div className={`${styles.rosarivo_regular} ${styles.contDate} }`}>
                                <h4>Sbado</h4>
                                <h4>14.12.2024</h4>
                                <h4>S 19h</h4>
                            </div>
                        </div>
                        <div className={styles.contEndss}>
                            <p>R. Visconde de Caravelas, 120 - Ribeira, Salvador - BA, 40420-160</p>
                            <h5 className={styles.tangerine_regular}>Cerimnial Casa Real </h5>
                        </div>
                        <div
                        className={styles.contQRCODE}>
                            <h4
                            className={styles.loraR}
                            >Este QRcode corresponde ad entrada de </h4>
                            <h4
                            className={styles.lora}
                            >{UsuarioLogado && UsuarioLogado[0].nome}</h4>




                            <div
                            className={styles.contQR}
                            >
                                <QRCode
                                    size={256}
                                    className={styles.qrcode}
                                    value={`/convite/${user && user.id}`}
                                />
                            </div>






                            <h5>Contamos com sua presena!</h5>
                            <p
                            className={styles.att}
                            >Este convite  individual e intransfervel.</p>
                
                        </div>
                        <div className={styles.contMap}>
                            <h5>Localizao</h5>

                            <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3888.838174674284!2d-38.5028202257728!3d-12.918120358734427!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x7160fee02a3a17b%3A0x521e503d95e2424a!2sEspa%C3%A7o%20Casa%20Real!5e0!3m2!1sen!2sbr!4v1726326437248!5m2!1sen!2sbr"className={styles.map} allowfullscreen="" loading="lazy" referrerpolicy="no-referrer-when-downgrade"></iframe>
                        </div>
                        <button
                        onClick={Logout}
                        className={styles.buttonLogout}
                        >SAIR</button>
                        </div>
                    </div>
                </div>
            :
            <FormAnalise/>
            }
        </>
    )
}